enter;\n  background-color: $bg-tint;\n  border: 1px solid $border-color;\n  border-radius: $small-border-radius;\n  margin-right: 8px;\n  filter: grayscale(0.5);\n  font-family: $main-font;\n}\n\n.note {\n  font-size: 12px;\n  color: $em-color;\n  margin-bottom: 8px;\n  font-style: italic;\n}\n\n.edit-button,\n.delete-button {\n  background: none;\n  border: none;\n  font-size: 16px;\n  cursor: pointer;\n  padding: 0;\n  width: $button-size;\n  height: $button-size;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin-right: 5px;\n  opacity: 0.7;\n  transition: opacity 0.2s;\n\n  &:hover {\n    opacity: 1;\n  }\n}\n\n.delete-button {\n  color: rgba(255, 80, 80, 0.9);\n}\n\n.add_button {\n  width: 100%;\n  margin: 10px 0;\n  padding: 8px;\n}\n\n.drag-handle {\n  cursor: grab;\n  margin-right: 8px;\n  color: var(--SmartThemeEmColor);\n  opacity: 0.6;\n  transition: opacity 0.3s;\n\n  &:hover {\n    opacity: 1;\n  }\n\n  &:active {\n    cursor: grabbing;\n  }\n}\n\n.modal-overlay {\n  position: fixed;\n  inset: 0;\n  width: 100%;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.5);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 9999;\n}\n\n.modal-content {\n  background-color: var(--SmartThemeBlurTintColor);\n  border: 1px solid $border-color;\n  border-radius: 8px;\n  padding: 20px;\n  min-width: 300px;\n  max-width: 90vw;\n  max-height: 90vh;\n  overflow-y: auto;\n\n  h3 {\n    margin: 0 0 15px 0;\n    color: $text-color;\n  }\n}\n\n.form-group {\n  margin-bottom: 15px;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n\n  label {\n    flex-shrink: 0;\n    min-width: 90px;\n    color: $text-color;\n    font-weight: 500;\n  }\n\n  input[type='text'],\n  input[type='number'] {\n    flex: 1;\n    padding: 8px 12px;\n    border: 1px solid $border-color;\n    border-radius: $border-radius;\n    background-color: var(--SmartThemeBlurTintColor);\n    color: $text-color;\n    font-size: 14px;\n\n    &:focus {\n      outline: none;\n      border-color: var(--SmartThemeEmColor);\n    }\n  }\n\n  input[type='checkbox'] {\n    margin-right: 8px;\n    transform: scale(1.2);\n  }\n\n  select {\n    flex: 1;\n    padding: 8px 12px;\n    border: 1px solid $border-color;\n    border-radius: $border-radius;\n    background-color: var(--SmartThemeBlurTintColor);\n    color: $text-color;\n    font-size: 14px;\n    cursor: pointer;\n\n    &:focus {\n      outline: none;\n      border-color: var(--SmartThemeEmColor);\n    }\n\n    option {\n      background-color: var(--SmartThemeBlurTintColor);\n      color: $text-color;\n    }\n  }\n\n  .cursor-position-wrapper {\n    display: flex;\n    gap: 8px;\n    align-items: center;\n\n    select {\n      flex: 1;\n      min-width: 0;\n    }\n\n    .custom-cursor-input {\n      width: 100px;\n      flex-shrink: 0;\n    }\n  }\n}\n\n.modal-buttons {\n  display: flex;\n  gap: 10px;\n  justify-content: flex-end;\n  margin-top: 20px;\n\n  button {\n    padding: 8px 16px;\n    border: none;\n    border-radius: $border-radius;\n    cursor: pointer;\n    font-size: 14px;\n    transition: background-color 0.2s;\n\n    &.button-cancel {\n      background-color: transparent;\n      border: 1px solid $border-color;\n      color: $text-color;\n\n      &:hover {\n        background-color: rgba(255, 255, 255, 0.1);\n      }\n    }\n\n    &.button-save {\n      background-color: transparent;\n      border: 1px solid $border-color;\n      color: $text-color;\n\n      &:hover {\n        background-color: rgba(255, 255, 255, 0.1);\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
